.abouts{
    display: flex;
    color:white;
    font-family: Mukta,-apple-system,BlinkMacSystemFont,Segoe UI,Roboto,Oxygen,Ubuntu,Cantarell,Fira Sans,Droid Sans,Helvetica Neue,sans-serif;
    padding: 50px;
    margin: auto;
}

.my-img{
    height:240px;
    width:240px;
    border-radius: 50%;
}

@media screen and (max-width:480px)
{
    .abouts{
        width:100%;
        display: flex;
        flex-direction: column;
    }
    .my-img{
        order:-1;
        width:200px;
        height:200px;
        border-radius: 50%;
    }
}

@media screen and (min-width:481px) and (max-width:780px)
{
    .abouts{
        width:100%;
        display: flex;
        flex-direction: column;
    }
    .my-img{
        order:-1;
        width:220px;
        height:220px;
    }
}
